<template lang='pug'>
v-tabs(app)
  v-tab –Ø–∫ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á
  v-tab-item
    v-card
      v-card-text
        v-alert(type='success' v-if='success' :value='true') –í–∏ —É—Å–ø—ñ—à–Ω–æ –∞–≤—Ç–æ—Ä–∏–∑—É–≤–∞–ª–∏—Å—å!
        v-alert(type='error' v-if='error' :value='true') {{ error }}
        v-form(v-model='validUser')
          v-text-field(
            prepend-icon='person'
            v-model='userLogin'
            label='–í–∞—à –ª–æ–≥—ñ–Ω'
            :rules='rules'
            required)
          file-input(
            @file="privateKeyFile = $event"
            label='–ó–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ –ø—Ä–∏–≤–∞—Ç–Ω–∏–π –∫–ª—é—á'
            :required='true'
            accept='.key')
          v-text-field(
            prepend-icon='lock'
            v-model='passphrase'
            type='password'
            label='–ü–∞—Ä–æ–ª—å–Ω–∞ —Ñ—Ä–∞–∑–∞ –∫–ª—é—á–∞'
            :rules='rules'
            required)
      v-card-actions
        v-spacer
        v-btn(@click.prevent='signIn' :disabled='!validUser' color='primary') –ê–≤—Ç–æ—Ä–∏–∑—É–≤–∞—Ç–∏—Å—å
  v-tab –Ø–∫ –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä
  v-tab-item
    v-card
      v-card-text
        v-alert(type='success' v-if='success' :value='true') –í–∏ —É—Å–ø—ñ—à–Ω–æ –∞–≤—Ç–æ—Ä–∏–∑—É–≤–∞–ª–∏—Å—å!
        v-alert(type='error' v-if='error' :value='true') {{ error }}
        v-form(v-model='validAdmin')
          v-text-field(
            prepend-icon='person'
            v-model='userLogin'
            type='text'
            label='–í–∞—à –ª–æ–≥—ñ–Ω'
            :rules='rules'
            required)
          v-text-field(
            prepend-icon='lock'
            v-model='userPassword'
            type='password'
            label='–í–∞—à –ø–∞—Ä–æ–ª—å'
            :rules='rules'
            required)
      v-card-actions
        v-spacer
        v-btn(@click.prevent='signInAdmin' :disabled='!validAdmin' color='primary') –ê–≤—Ç–æ—Ä–∏–∑—É–≤–∞—Ç–∏—Å—å
</template>

<script>
import { mapActions } from 'vuex'
import fileInput from '../../_common/fileInput'
export default {
  components: {
    fileInput
  },
  data () {
    return {
      userLogin: '',
      userPassword: '',
      passphrase: '',
      privateKeyFile: null,
      success: false,
      error: '',
      validUser: false,
      validAdmin: false,
      rules: [ v => !!v || '–û–±–æ–≤ º—è–∑–∫–æ–≤–µ –ø–æ–ª–µ!' ]
    }
  },
  methods: {
    ...mapActions('usersStore', ['getCurrentUser']),
    ...mapActions(['logIn', 'logInAdmin']),
    signIn (e) {
      this.logIn({
        login: this.userLogin,
        passphrase: this.passphrase,
        privateKeyFile: this.privateKeyFile
      })
        .then(() => {
          this.success = true
          this.error = ''
          this.$router.push('edms')
        })
        .catch(err => {
          this.success = false
          this.error = err.message
        })
    },
    signInAdmin (e) {
      this.logInAdmin({
        userLogin: this.userLogin,
        userPassword: this.userPassword
      })
        .then(() => {
          this.success = true
          this.error = ''
          this.getCurrentUser()
            .then(() => {
              this.$router.push('edms/admin')
            })
        })
        .catch(err => {
          this.success = false
          this.error = err.message
        })
    }
  }
}
</script>



