<template lang='pug'>
  v-container(fluid grid-list-md app).bg-simple
    v-layout(row wrap)
      v-flex(xs12)
        v-card.px-3.py-3
          v-subheader.headline {{ document.title }}
          v-subheader –î–∞—Ç–∞ –ø–µ—Ä—à–æ—ó –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó {{ toDateString(+document.date) }}
          v-subheader(v-if='document.versions && document.versions.lenght > 1') –ü–æ—Ç–æ—á–Ω–∞ –≤–µ—Ä—Å—ñ—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ {{ document.versions[0].version }}
          v-subheader(v-if='document.versions && document.versions.lenght > 1') –î–∞—Ç–∞ –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó –≤–µ—Ä—Å—ñ—Ö {{ document.versions[0].version }}: {{ document.versions[0].date }}
      v-flex(xs12)
        doc-tabs
      v-flex(xs12 lg6)
        signers-list(:rejected='false')
      // document info
      v-flex(xs12 lg6)
        v-alert(
          type='success'
          v-if='document.globalStatus === "resolved"'
          :value='true'
          ) –î–æ–∫—É–º–µ–Ω—Ç —É—Å–ø—ñ—à–Ω–æ –ø—ñ–¥–ø–∏—Å–∞, –π–æ–≥–æ –º–æ–∂–Ω–∞ –∑–Ω–∞–π—Ç–∏ —É
          router-link(:to='"/edms/archive/" + document._id') –ê—Ä—Ö—ñ–≤—ñ
        v-alert(
          variant='primary'
          v-if='document.globalStatus === "waiting"'
          :value='true'
          ) –î–æ–∫—É–º–µ–Ω—Ç –Ω–∞ —Å—Ç–∞–¥—ñ—ó –ø–µ—Ä–µ–≥–ª—è–¥—É. –ó–∞—Ä–∞–∑ –π–æ–≥–æ –ø–µ—Ä–µ–≥–ª—è–¥–∞—î: {{ closerViewer }}
        div(v-if='document.globalStatus === "rejected"')
          v-alert(
            variant='error'
            :value='true'
            ) –î–æ–∫—É–º–µ–Ω—Ç –≤—ñ–¥–º–æ–≤–ª–µ–Ω –≤ –ø—ñ–¥–ø–∏—Å—ñ. –í–∏ –º–æ–∂–µ—Ç–µ –µ–≥–æ –¥–æ—Ä–æ–±–∏—Ç–∏ –∞–±–æ –∑–∞–∫—Ä–∏—Ç–∏.
            v-card
              v-card-text {{ document.versions[0].rejectReason }}
          v-btn(@click='closeDocument' color='error') –ó–∞–∫—Ä–∏—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç
          v-btn(:to='"/edms/myDocumentEdit/" + id' color='primary') –î–æ—Ä–æ–±–∏—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      id: this.$router.currentRoute.params.id
    }
  },
  computed: {
    ...mapGetters('docsStore', ['document']),
    closerViewer () {
      return this.document.routes.find(route => route.status === 'waiting').author
    }
  },
  methods: {
    ...mapActions('docsStore', ['getMyDocumentById', 'closeDocumentById']),
    toDateString,
    closeDocument () {
      this.closeDocumentById(this.id)
    }
  },
  created () {
    this.getMyDocumentById(this.id)
      .catch(e => {
        this.$router.push('404')
      })
  },
  components: {
    docTabs: require('../_common/docTabs'),
    signersList: require('../_common/signersList')
  }
}
</script>



