<template lang='pug'>
  v-form(v-model='valid')
    v-subheader.headline –°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤—É –≥—Ä—É–ø—É
    v-text-field(
      type='text'
      v-model='groupName'
      required
      :rules='rules'
      label='–í–≤–µ–¥—ñ—Ç—å –Ω–∞–∑–≤—É –≥—Ä—É–ø–∏'
      placeholder='–ù–∞–∑–≤–∞ –≥—Ä—É–ø–∏')

    v-text-field(
      type='email'
      v-model='adminEmail'
      placeholder='example@gmail.com'
      label='E-mail –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–∏'
      description='–£–≤–∞–≥–∞! –ù–∞ —Ü–µ–π –∞–¥—Ä–µ—Å –±—É–¥—É—Ç—å –≤—ñ–¥—ñ—Å–ª–∞–Ω—ñ –¥–∞–Ω—ñ —Ñ–≤—Ç–æ—Ä–∏–∑–∞—Ü—ñ—ó!'
      required
      :rules='rules')

    v-btn(@click.prevent='createGroup' color='primary' :disabled='!valid') –°—Ç–≤–æ—Ä–∏—Ç–∏ –≥—Ä—É–ø—É

    v-dialog(v-model='showAlert')
      v-card
        v-card-title
          v-subheader.headline –ü–µ—Ä–µ–≤—ñ—Ä—Ç–µ –¥–∞–Ω—ñ!
        v-card-text
          v-text-field(
            v-model='groupName'
            label='–ù–∞–∑–≤–∞ –≥—Ä—É–ø–ø–∏'
            readonly)
          v-text-field(
            v-model='adminEmail'
            readonly
            label='Email –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞'
            description='–ù–∞ —Ü—é –ø–æ—à—Ç—É –±—É–¥—É—Ç—å –≤—ñ–¥—ñ—Å–ª–∞–Ω—ñ –ª–æ–≥—ñ–Ω —Ç–∞ –ø–∞—Ä–æ–ª—å')
        v-card-actions
          v-btn(@click='submitForm' color='primary') –í—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏

</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      groupName: '',
      adminEmail: '',
      info: '',
      valid: false,
      rules: [ v => !!v || '–ü–æ–ª–µ –æ–±–æ–≤ º—è–∑–∫–æ–≤–µ!' ],
      showAlert: false
    }
  },
  methods: {
    ...mapActions('groupsStore', ['createNewGroup']),
    createGroup () {
      this.showAlert = true
    },
    submitForm () {
      this.createNewGroup({ groupName: this.groupName, adminEmail: this.adminEmail })
        .then(response => {
          this.$refs.createGroupForm.reset()
          this.$emit('alert', response.message)
        })
        .catch(e => this.$emit('alert', e.message))
    }
  }
}
</script>
<style lang="sass" scoped>
.users-list
  max-height: 500px
  overflow-y: scroll
</style>



