<template lang='pug'>
  div
    v-subheader.headline –°–ø–∏—Å–æ–∫ –≥—Ä—É–ø
    v-tabs(dark)
      v-tab(v-for='group,i in groups' :key='i') {{ group.name + " –ö–æ—Ä–∏—Å—Ç—É–≤–∞—á—ñ–≤: " + group.users.length }}
      v-tab-item(v-for='group,i in groups' :key='i')
        template(v-for='user in group.users')
          v-card
            v-card-title
              v-subheader {{ user.author ? user.author : '–ù–µ –∑–∞—Ä–µ—î—Å—Ç—Ä–æ–≤–∞–Ω' }}
            v-card-text
              p.mb-0 –†–æ–ª—å: {{ user.role ? user.role : '–ù–µ –∑–∞—Ä–µ—î—Å—Ç—Ä–æ–≤–∞–Ω' }}
              p.mb-0 –î–∞—Ç–∞ —Äe—î—Å—Ç—Ä–∞—Ü—ñ—ó: {{ toDateString(+user.dateRegistration) }}
              p.mb-0 Email: {{ user.email }}
              p.mb-0 –õ–æ–≥—ñ–Ω: {{ user.login }}

            v-card-actions(v-if='user.role === "Admin"')
              v-btn(@click='openSendModal(user._id, user.email)' color='primary') –í—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏ –¥–æ—Å—Ç—É–ø–∏ —â–∞ —Ä–∞–∑
              v-btn(@click='deleteAdmin(user._id, group._id)' color='error') –í–∏–¥–∞–ª–∏—Ç–∏ –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞
          v-divider
    v-dialog(v-model='sendModal')
      v-card
        v-card-title
          v-subheader –í–∫–∞–∂—ñ—Ç—å –ø–æ—á—Ç—É, –Ω–∞ —è–∫—É –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç—å—Å—è –¥–æ—Å—Ç—É–ø
        v-card-text
          v-text-field(type='email' v-model='admin.email' label='Email –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞')
        v-card-actions
          v-btn(@click.prevent='submitSend' color='primary') –í—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏

</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../../modulesJs/toDateString'
export default {
  data () {
    return {
      admin: {
        email: '',
        adminId: ''
      },
      sendModal: false
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['groups'])
  },
  methods: {
    ...mapActions('groupsStore',
      [
        'getAllGroups',
        'removeAdmin',
        'sendInviteAdmin'
      ]),
    toDateString,
    deleteAdmin (adminId, groupId) {
      this.removeAdmin({ adminId, groupId })
    },
    openSendModal (adminId, adminEmail) {
      this.admin = {
        email: adminEmail,
        adminId
      }
      this.sendModal = true
    },
    submitSend () {
      this.sendInviteAdmin(this.admin)
        .then(response => this.$emit('alert', response.message))
        .catch(e => this.$emit('alert', e.message))
    }
  },
  created () {
    this.getAllGroups()
  }
}
</script>



