<template lang='pug'>
  div
    v-card.px-3.py-3
      v-subheader –°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤–∏–π —Å–ø–∏—Å–æ–∫ –≤—ñ–∑
      v-btn(@click='showModal = true' color='secondary') –í—ñ–¥–∫—Ä–∏—Ç–∏ —Ä–µ–¥–∞–∫—Ç–æ—Ä
    v-dialog(ref='routesEditor' title='–°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤–∏–π –º–∞—Ä—à—Ä—É—Ç' v-model='showModal')
      v-card.px-3.py-3
        v-form(v-model='valid' ref='routesEditorForm')
          v-text-field(
            v-model='presetName'
            required
            :rules='rules'
            label='–í–≤–µ–¥—ñ—Ç—å –Ω–∞–∑–≤—É —Å–ø–∏—Å–∫—É')
          choose-authors(
            @updateSelectedUsers='updatePresetRoute'
            :selectedUsers='selectedUsers')
          authors-list(:selectedUsers='selectedUsers' @updateSelectedUser='updatePresetRoute')
          v-btn(@click.prevent='createNewPreset' color='primary' :disabled='!valid || !selectedUsers.length') –°—Ç–≤–æ—Ä–∏—Ç–∏ –ú–∞—Ä—à—Ä—É—Ç
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      selectedUsers: [],
      presetName: '',
      showModal: false,
      rules: [ v => !!v || '–û–±–æ–≤ º—è–∑–∫–æ–≤–µ –ø–æ–ª–µ!' ],
      valid: false
    }
  },
  computed: mapGetters('usersStore', ['currentUser']),
  methods: {
    ...mapActions('docsStore', ['createPreset', 'getPresets']),
    updatePresetRoute (selectedUsers) {
      this.selectedUsers = selectedUsers
    },
    createNewPreset (e) {
      const newPreset = {
        title: this.presetName,
        routes: this.selectedUsers,
        group: this.currentUser.groupInvite
      }
      this.createPreset(newPreset)
        .then(response => {
          this.$refs.routesEditorForm.reset()
          this.selectedUsers = []
          this.showModal = false
          this.getPresets()
        })
    }
  },
  components: {
    chooseAuthors: require('./chooseAuthors'),
    authorsList: require('./authorsList')
  }
}
</script>



