<template lang='pug'>
  v-container(app fluid fill-height).bg-simple
    v-layout
      v-flex
        v-subheader.headline.mt-3 –î–æ–∫—É–º–µ–Ω—Ç–∏ –∫ –ø—ñ–¥–ø–∏—Å–∞–Ω–Ω—é
        v-alert(type='info' :value='!loaded') –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î—Ç—å—Å—è...
        v-alert(type='warning' :value='loaded && !sortingData.length') –ù–æ–≤–∏—Ö –¥–æ–∫—É–º–µ–Ω—Ç—ñ–≤ –¥–æ —Ä–æ–∑–≥–ª—è–¥—É –Ω–µ–º–∞—î
        template(v-for='preview in sortingData')
          v-card
            v-layout(row wrap)
              v-flex(xs12 md6)
                v-subheader.headline {{ preview.title }}
              v-flex(xs12 md6)
                v-subheader –î–∞—Ç–∞ –ø—É–±–ª—ñ–∫–∞—Ü—ñ—ó: {{ toDateString(+preview.date) }}
              v-flex(xs12 md6)
                v-subheader –ê–≤—Ç–æ—Ä –ø—É–±–ª—ñ–∫–∞—Ü—ñ—ó: {{ preview.author }}
              v-flex(xs12 md6)
                v-subheader –ü–æ—Ç–æ—á–Ω–∏–π –º–∞—Ä—à—Ä—É—Ç: {{ preview.state }} / {{ preview.total }}
              v-flex(xs12 md6)
                v-subheader –ß–∞—Å –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –≤—ñ–∑–∏: {{ incomingDate(preview) }}
              v-flex(xs12)
                v-btn(
                  :to='"/edms/" + preview._id' color='primary')
                  | –ü–µ—Ä–µ–π—Ç–∏
                  v-icon(right dark) exit_to_app
          v-divider
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      loaded: false
    }
  },
  computed: {
    ...mapGetters('docsStore', ['documents']),
    ...mapGetters('usersStore', ['currentUser']),
    sortingData () {
      if (!this.documents.length) return []
      return this.documents.slice(0).sort((prev, next) => prev.date <= next.date)
    }
  },
  methods: {
    ...mapActions('docsStore', ['getPreviews']),
    ...mapActions('usersStore', ['getCurrentUser']),
    toDateString,
    incomingDate (document) {
      const user = document.routes.find(
        route => route.author === this.currentUser.author
      )
      return this.toDateString(+user.dateIncoming)
    }
  },
  created () {
    this.getPreviews()
      .then(() => {
        this.loaded = true
      })
  }
}
</script>



