<template lang='pug'>
  v-form(v-model='valid')
    v-subheader.headline –°—Ç–≤–æ—Ä–∏—Ç–∏ –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–∏
    v-select(
      v-model='admin.groupId'
      :items='groupsOptions'
      label='–í–∏–±–µ—Ä—ñ—Ç—å –≥—Ä—É–ø—É:'
      description='–ì—Ä—É–ø–∞, –¥–ª—è —è–∫–æ—ó —Å—Ç–≤–æ—Ä–∏—Ç—å—Å—è –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä'
      required)
    v-text-field(
      type='email'
      v-model='admin.email'
      placeholder='example@gmail.com'
      label='–ï-mail –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–∏'
      description='–£–≤–∞–≥–∞! –ù–∞ —Ü–µ–π –∞–¥—Ä–µ—Å –±—É–¥—É—Ç—å –≤—ñ–¥—ñ—Å–ª–∞–Ω—ñ –¥–∞–Ω–Ω—ñ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü—ñ—ó!'
      required
      :rules='rules')

    v-btn(@click.prevent='createAdmin' color='primary' :disabled='!valid') –°—Ç–≤–æ—Ä–∏—Ç–∏ –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      admin: {
        email: '',
        groupId: ''
      },
      valid: false,
      rules: [ v => !!v || '–ü–æ–ª–µ –æ–±–æ–≤ º—è–∑–∫–æ–≤–µ!' ]
    }
  },
  methods: {
    ...mapActions('groupsStore', ['createNewAdmin']),
    createAdmin () {
      this.createNewAdmin({ ...this.admin })
        .then(response => {
          this.$refs.createAdminForm.reset()
          this.$emit('alert', response.message)
        })
        .catch(e => {
          this.$emit('alert', e.message)
        })
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['groups']),
    groupsOptions () {
      const resultOptions = [{ value: '', text: '–ù–µ –æ–±—Ä–∞–Ω–æ' }]
      for (let group of this.groups) {
        resultOptions.push({ value: group._id, text: group.name })
      }
      return resultOptions
    }
  }
}
</script>



