<template lang='pug'>
  v-container(app fluid fill-height grid-list-md).bg-simple
    v-layout(row wrap)
      v-flex(xs12)
        v-card.px-3.py-3
          v-subheader.headline {{ document.title }}
          div(v-if='document.author && document.date && document.versions')
            v-subheader –ê–≤—Ç–æ—Ä –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó {{ document.author}}
            v-subheader –î–∞—Ç–∞ –ø–µ—Ä—à–æ—ó –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó {{ toDateString(+document.date) }}
            div(v-if='document.versions.length > 1')
              v-subheader –ü–æ—Ç–æ—á–Ω–∞ –≤–µ—Ä—Å—ñ—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ {{ document.versions[0].version }}
              v-subheader –î–∞—Ç–∞ –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó –≤–µ—Ä—Å—ñ—ó {{ document.versions[0].version }}: {{ toDateString(+document.versions[0].date) }}
      v-flex(xs12)
        doc-tabs
      v-flex(xs12 lg6)
        signers-list(:rejected='rejected')
      v-flex(xs12 lg6)
        // sign form
        v-card.px-3.py-3
          v-form(v-model='valid' ref='signForm')
            v-subheader –û–±–µ—Ä—ñ—Ç—å –¥—ñ—é
            v-radio-group(
              v-model='selected'
              required
              :rules='rules')
              v-radio(
                label='–ü—ñ–¥–ø–∏—Å–∞—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç'
                value='resolve')
              v-radio(
                label='–í—ñ–¥–º–æ–≤–∏—Ç–∏ –≤ –ø—ñ–¥–ø–∏—Å—ñ'
                value='reject')
            v-text-field(
              v-model='comment'
              placeholder='–ó–∞–ª–∏—à—Ç–µ –∫–æ–º–µ–Ω—Ç–∞—Ä'
              multi-line
              :rules='rules'
              required)
            v-btn(@click.prevent='submitDoc' :disabled='!valid || submitting' color='primary') –í—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏

    v-dialog(v-model="alert" max-width="290")
      v-card
        v-card-text {{ infoAlert }}
        v-card-actions
          v-btn(flat @click.native='alert = false') –ó–∞–∫—Ä–∏—Ç–∏
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      // get id of current document
      id: this.$router.currentRoute.params.id,
      // current status of doc
      selected: '',
      comment: '',
      infoAlert: '',
      rejected: false,
      valid: false,
      rules: [ v => !!v || '–ü–æ–ª–µ –æ–±–æ–≤ º—è–∑–∫–æ–≤–µ!' ],
      submitting: false,
      alert: false
    }
  },
  computed: {
    ...mapGetters('docsStore', ['document']),
    ...mapGetters('usersStore', ['currentUser']),
    ...mapGetters(['privateKey'])
  },
  methods: {
    ...mapActions('docsStore', ['getDocumentById', 'postVote']),
    toDateString,
    submitDoc (e) {
      if (this.selected && this.comment) {
        // post our vote to server
        this.submitting = true
        this.postVote({
          id: this.id,
          vote: this.selected,
          comment: this.comment,
          author: this.currentUser
        })
          .then(response => {
            if (this.selected === 'reject') {
              this.rejected = true
            }
            this.showAlert(response.message)
            this.$refs.signForm.reset()
            this.getDocumentById(this.id)
            this.submitting = false
          })
          .catch(e => {
            this.showAlert(`–í–∏–Ω–∏–∫–ª–∞ –ø–æ–º–∏–ª–∫–∞: ${e.message}`)
            this.submitting = false
          })
      }
    },
    showAlert (title) {
      this.infoAlert = title
      this.alert = true
    }
  },
  created () {
    this.getDocumentById(this.id)
      .catch(() => {
        this.$router.push('404')
      })
  },
  components: {
    docTabs: require('../_common/docTabs'),
    signersList: require('../_common/signersList')
  }
}
</script>



