<template lang='pug'>
  v-container(fluid grid-list-md app).bg-simple
    v-alert(type='info' :value='!loaded') –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î—Ç—å—Å—è...
    v-alert(type='warning' :value='loaded && !sortingData.length') –£ –≤–∞—Å –Ω–µ–º–∞—î –æ–ø—É–±–ª—ñ–∫–æ–≤–∞–Ω–∏—Ö –¥–æ–∫—É–º–µ–Ω—Ç—ñ–≤!
    template(v-for='preview in sortingData')
      v-card(:color='globalStatus(preview).color').px-3.py-3
        v-subheader.headline {{ preview.title }}
        v-subheader {{ '–î–∞—Ç–∞ –ø—É–±–ª—ñ–∫–∞—Ü—ñ—ó: ' + toDateString(+preview.date) }}
        v-subheader –°—Ç–∞—Ç—É—Å: {{ globalStatus(preview).status }}
        v-btn(:to='"/edms/myDocuments/" + preview._id' color='primary')
          | –ü–µ—Ä–µ–π—Ç–∏
          v-icon(right dark) exit_to_app
      v-divider
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      loaded: false
    }
  },
  computed: {
    ...mapGetters('docsStore', ['documents']),
    sortingData () {
      return this.documents.slice(0).sort((prev, next) => prev.date <= next.date)
    }
  },
  methods: {
    ...mapActions('docsStore', ['getOurDocuments']),
    toDateString,
    globalStatus (doc) {
      switch (doc.globalStatus) {
        case 'resolved': return { color: 'success', status: '–ü—Ä–∏–π–Ω—è—Ç–∏–π' }
        case 'rejected': return { color: 'error', status: '–í—ñ–¥–º–æ–≤–ª–µ–Ω' }
        case 'waiting': return { color: 'info', status: '–ù–∞ —Ä–æ–∑–≥–ª—è–¥–∞–Ω–Ω—ñ' }
        case 'archived': return { color: 'secondary', status: '–ó–∞–∫—Ä–∏—Ç–∏–π' }
      }
    }
  },
  created () {
    this.getOurDocuments()
      .then(() => {
        this.loaded = true
      })
  }
}
</script>



